//bootstrap
import { Container } from '@server/bootstrap'
import { ENVKEY } from '@config/env'
//module
import { createTestClient } from 'apollo-server-testing'
import { Express } from 'express'
import { ApolloServer, gql } from 'apollo-server-express'

// beforeAll(async ()=>{
	
// })


describe('Oracle db connection', ()=>{
	
	it('connect', async (done)=>{
		const [app, apollo]:[Express, ApolloServer] = await Container.getValue(ENVKEY.SERVER.EXPRESS.APP)()
		const { query, mutate } = createTestClient(apollo)

		const LECUTRE = gql`
			query lectures {
				cotent_no
			}
		`

		const { data:lecturs } = await query({ query:LECUTRE })
		console.log(lecturs) 	
	})

})